<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="editMode"
           @edit="onEdit">

    <div class="cp-row">
      <label for="corpTaxId">{{$t('Corporate Tax ID')}}:</label>
      <input id="corpTaxId"
             type="text"
             class="form-control"
             v-model="cpModel.taxNumber"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="corpTaxFillingDate">{{$t('Filing Deadline')}}:</label>
      <RecurringDateModal
        id="corpTaxFillingDate"
        v-model="cpModel.fillingDate"
        name="corpTaxFillingDate"
        :class="[{'form-control': true, 'is-invalid': errors.has('corpTaxFillingDate'), 'inactive': !editMode}]"
        :disabled="!editMode"
      ></RecurringDateModal>
    </div>

    <div class="cp-row">
      <label for="corpTaxDueDate">{{$t('Due Date')}}:</label>
      <RecurringDateModal
        id="corpTaxDueDate"
        v-model="cpModel.dueDate"
        name="corpTaxDueDate"
        :class="[{'form-control': true, 'is-invalid': errors.has('corpTaxDueDate'), 'inactive': !editMode}]"
        :disabled="!editMode"
      ></RecurringDateModal>
    </div>

    <div class="cp-row">
      <label for="corpTaxAdvancedPayments">Advanced Payments:</label>
      <RecurringDateModal
        id="corpTaxAdvancedPayments"
        v-model="cpModel.advancedPayments"
        name="corpTaxAdvancedPayments"
        :class="[{'form-control': true, 'is-invalid': errors.has('corpTaxAdvancedPayments'), 'inactive': !editMode}]"
        :disabled="!editMode"
      ></RecurringDateModal>
    </div>

  </cp-form>
</template>


<script>
import singlePropSetMixin from './singlePropSetMixin'
import RecurringDateModal from '@/components-app/modals/RecurringDateModal'
// import RecurringDateSelect from '@/components-app/selects/RecurringDateSelect'

const SET_NAME = 'corpTax'

export default {
  components: {
    RecurringDateModal,
  },
  mixins: [singlePropSetMixin(SET_NAME)],
  methods: {
    // remove() {
    //   var updateData = {}
    //   updateData[SET_NAME] = Object.keys(this.cProp).reduce((obj, key) => {
    //     obj[key] = null
    //     return obj
    //   }, {})
    //
    //   return this.$store.dispatch('updateSelCompany', updateData)
    //     .then(() => {
    //       this.$emit('remove')
    //     })
    // },
  },
}
</script>



